@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

.table-container {
  width: 100%;
  margin-top: 20px;
}

.table-container table {
  border-collapse: collapse;

  width: 100%;
}

.table-container td,
th {
  border: 1px solid #dddddd;

  text-align: center;
  padding: 8px;
}

.table-container th {
  background-color: #006266;
  color: #fff;
}

.table-container tr:nth-child(even) {
  background-color: #dddddd;
}
.table-container tr:nth-child(odd) {
  background-color: #fff;
}

.table-container table button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  border-radius: 5px;
  background-color: #c0392b;
  padding: 10px 25px;
  color: #fff;
  cursor: pointer;
  font-size: 0.8rem;
}

.table-container table button:first-child {
  margin-right: 10px;
  background-color: #1289a7;
}

@media (max-width: 450px) {
  .table-container table button {
    width: 100%;
  }
}

.app {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px auto;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #1b1b1b;
  border-radius: 10px;
}

.app h1 {
  margin: 0 auto;
  margin-bottom: 20px;
  color: #fff;
}

.update-popup,
.delete-popup-container {
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete-popup-container .delete-popup {
  width: 250px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}

.delete-popup button {
  padding: 10px 25px;
  border: none;
  border-radius: 5px;
  background-color: #c0392b;
  color: #fff;
  cursor: pointer;
}

.delete-popup .yes-btn {
  margin-right: 10px;
  background-color: #009432;
}

.delete-popup p {
  margin-bottom: 20px;
}

