.form {
  max-width: 500px;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
}

.form input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 1rem;
  margin: 8px auto 10px;
}

.form button {
  width: 100%;
  border: none;
  border-radius: 5px;
  padding: 10px;
  background-color: #009432;
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
}

.form .submit-btn {
  margin-bottom: 8px;
}

.form .cancel-btn {
  background-color: #c0392b;
}

.num-length-err {
  position: fixed;
  z-index: 1000;
  top: 35px;
  /* margin: 10px auto 10px; */
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #fff;
  padding: 10px 60px;
  border-radius: 5px;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-animation: error 1s;
          animation: error 1s;
}

@-webkit-keyframes error {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes error {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.spinner-container {
  width: 100%;
  margin-top: 20px;
  text-align: center;
}

